//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


$familyRoboto: 'Roboto';
$familyCirce: 'Circe';

@mixin roboto {
	font-family: 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; }
@mixin circe {
	font-family: 'Circe', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;

 }	//CIRCE
@font-face {
	@include fontdefault(normal, 800, $familyCirce);
	@include fontpath('Circe Extra Bold', 'circe-extrabold', Circe-ExtraBold); }

@font-face {
	@include fontdefault(normal, 700, $familyCirce);
	@include fontpath('Circe Bold', 'circe-bold', Circe-Bold); }

@font-face {
	@include fontdefault(normal, 400, $familyCirce);
	@include fontpath('Circe', 'circe-regular', Circe-Regular);

 }	//ROBOTO
@font-face {
	@include fontdefault(normal, 700, $familyRoboto);
	@include fontpath('Roboto Bold', 'roboto-bold', Roboto-Bold); }
@font-face {
	@include fontdefault(italic, 700, $familyRoboto);
	@include fontpath('Roboto Bold Italic', 'roboto-bolditalic', Roboto-BoldItalic); }

@font-face {
	@include fontdefault(normal, 500, $familyRoboto);
	@include fontpath('Roboto Medium', 'roboto-medium', Roboto-Medium); }
@font-face {
	@include fontdefault(italic, 500, $familyRoboto);
	@include fontpath('Roboto Medium Italic', 'roboto-mediumItalic', Roboto-MediumItalic); }

@font-face {
	@include fontdefault(normal, 400, $familyRoboto);
	@include fontpath('Roboto', 'roboto-regular', Roboto-Regular); }
@font-face {
	@include fontdefault(italic, 400, $familyRoboto);
	@include fontpath('Roboto Italic', 'roboto-italic', Roboto-Italic); }
