body {
  background: #FFF;
  line-height: 1.4;
}

.inner {
  max-width: 1595px;
  margin: 0 auto;
  padding: 0 40px;
}

.inner-lg {
  max-width: 1675px;
  margin: 0 auto;
  padding: 0 40px;
}

.out {
  position: relative;
  overflow: hidden;
}

.block {
  display: flex;
}

.delivery-info {
  text-align: center;
  padding: 64px 0 32px;
  color: #483d3c;
}

.delivery-info__title {
  color: #6aa943;
}

.delivery-info__wrap {
  margin: 25px -46px 0;
  display: flex;
  justify-content: center;
}

.delivery-info__item {
  padding: 0 46px;
}

.delivery-info__btn {
  text-align: center;
  margin-top: 30px;
  min-width: 170px;
}

.delivery-item {
  max-width: 220px;
  text-align: center;
}

.delivery-item__img-wrap {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 200px;
}

.delivery-item__text {
  margin-top: 30px;
  font-size: 27px;
  font-weight: 700;
}

.delivery-item__text span {
  text-transform: uppercase;
  display: block;
}

.delivery-item_big {
  max-width: 350px;
}

.btn {
  padding: 12px 28px;
  font-family: 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  vertical-align: top;
  display: inline-block;
  border-radius: 20.5px;
  background-color: #d0661b;
  position: relative;
  overflow: hidden;
  transition: background-color .3s, color .3s;
}

.btn:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000;
  opacity: 0;
  transition: opacity .3s;
}

.btn span {
  z-index: 2;
}

.no-touch .btn:hover:before {
  opacity: 0.2;
}

.btn_md {
  min-width: 170px;
}

.info {
  padding: 32px 0;
}

.info-item {
  display: flex;
  align-items: center;
  position: relative;
}

.info-item:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(231, 176, 92, 0.1);
  top: 0;
  left: 50%;
  z-index: -1;
}

.info-item:not(:last-child) {
  margin-bottom: 27px;
}

.info-item__img {
  max-width: 1065px;
}

.info-item_order .info-item__img {
  order: 2;
}

.info-item__main {
  max-width: 250px;
  margin: 0 auto;
}

.info-item__btn {
  margin-top: 50px;
}

.info-item.info-item_order:before {
  left: -50%;
}

.stock {
  padding: 40px 0;
  background-color: rgba(106, 169, 67, 0.1);
}

.stock__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.stock__list {
  display: flex;
  margin-top: 35px;
}

.stock__link {
  color: #000;
  display: inline-block;
  text-decoration: none;
}

.stock-item {
  max-width: 400px;
  font-size: 25px;
  height: 100%;
  background-color: transparent;
  transition: background-color .3s;
}

.no-touch .stock-item:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.stock-item__img-wrap img {
  width: 100%;
}

.stock-item__text {
  padding: 20px 12.5% 0 15%;
}

.stock-item__date {
  font-weight: 700;
}

.title {
  font-family: 'Circe', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
}
