.stock {
    padding: 40px 0;
    background-color: $green-bg;
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between; }
    &__list {
        display: flex;
        margin-top: 35px; }
    &__link {
        color: $black;
        display: inline-block;
        text-decoration: none; } }

.stock-item {
    max-width: 400px;
    font-size: 25px;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    transition: background-color .3s;
    @include hover {
        background-color: rgba(0, 0, 0, 0.1); }
    &__img-wrap {
        img {
            width: 100%; } }
    &__text {
        padding: 20px 12.5% 0 15%; }
    &__date {
        font-weight: 700; } }
