.delivery-info {
    text-align: center;
    padding: 64px 0 32px;
    color: $brown;
    &__title {
        color: $green; }
    &__wrap {
        margin: 25px -46px 0;
        display: flex;
        justify-content: center; }
    &__item {
        padding: 0 46px; }
    &__btn {
        text-align: center;
        margin-top: 30px;
        min-width: 170px; } }


.delivery-item {
    max-width: 220px;
    text-align: center;
    &__img-wrap {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        min-height: 200px; }
    &__text {
        margin-top: 30px;
        @include f(27px, 700);
        span {
            text-transform: uppercase;
            display: block; } } }
.delivery-item_big {
    max-width: 350px; }
